// @flow

import React, { Fragment } from "react";
import ReactDOM from "react-dom";
import { createGlobalStyle } from "styled-components";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/es/styles";
import { hot } from "react-hot-loader";
import App from "./containers/App";
import cstm from "./fonts/CSTM-Xprmntl.woff";
import cstm2 from "./fonts/CSTM-Xprmntl.woff2";
import bebas from "./fonts/bebasneue_bold-webfont.woff";
import bebas2 from "./fonts/bebasneue_bold-webfont.woff2";
import rubik from "./fonts/rubik-black-webfont.woff";
import rubik2 from "./fonts/rubik-black-webfont.woff2";

const theme = createMuiTheme({
  overrides: {
    MuiTab: {
      selected: {
        color: "#00ff00 !important"
      }
    }
  },
  typography: {
    fontFamily: "Montserrat, sans"
  },
  palette: {
    type: "dark",
    primary: {
      main: "#ffdb4d"
    },
    secondary: {
      main: "#fff"
    }
  }
});

const GlobalStyle = createGlobalStyle`
  body, html {
    margin: 0;
    min-height: 100vh;
    background: #1b1b1b;
  }

  @font-face {
    font-family: 'CSTMXPRMNTL0101';
    src: url(${cstm2}) format('woff2'), url(${cstm}) format('woff');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'bebas_neuebold';
    src: url(${bebas2}) format('woff2'), url(${bebas}) format('woff');
    font-weight: normal;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'rubikblack';
    src: url(${rubik2}) format('woff2'), url(${rubik}) format('woff');
    font-weight: normal;
    font-style: normal;
  }
`;

const container = document.getElementById("root");
const Root = () => (
  <Fragment>
    <GlobalStyle />
    <MuiThemeProvider theme={theme}>
      <App />
    </MuiThemeProvider>
  </Fragment>
);

const El = hot(module)(Root);

if (container != null) {
  ReactDOM.render(<El />, container);
}
